import React, { useEffect, useState } from 'react';
import styles from './MainPage.module.css'; 
import { useNavigate } from 'react-router-dom';
import upload_icon from '../img/upload_icon.png';
import download_icon from '../img/download_icon.png'
import { getCookie, removeCookie } from '../utils/CookieUtil';

import Chatbot from 'react-chatbot-kit'; 
import 'react-chatbot-kit/build/main.css';

import config from "../chatbot/config";
import MessageParser from '../chatbot/MessageParser';
import ActionProvider from '../chatbot/ActionProvider';

import './chatbot.css';


const MainPage = () => {
    const navigate = useNavigate();

    const refreshPage = () => {
      window.location.reload();
    };
    const learnClick = () => {
      navigate('/learn'); 
      };
    const logInClick = () => {
      navigate('/login');
    };
    const signUpClick = () => {
      navigate('/signup');
    };

    const [isLoggedIn, setIsLoggedIn] = useState(false);

    useEffect(() => {
      const accessToken = getCookie("token");
      if (accessToken && localStorage.getItem('accessToken', accessToken)) {
        setIsLoggedIn(true);
      } else {
        setIsLoggedIn(false);
      }
      
    }, []);

    const handleLogout = () => {
      localStorage.removeItem('accessToken');
      localStorage.removeItem('refreshToken');
      localStorage.removeItem('decodedToken');

      removeCookie("token");
      setIsLoggedIn(false);
      alert(' .');
    };

      return (
        <div className={styles.container}>
          <div className="header">
            <button className={`${styles.mainbtn}`} onClick={refreshPage}><h1>Reader.</h1></button>
            {isLoggedIn ? ( 
            <button className={`${styles.signbtn}`} onClick={handleLogout}></button>
            ) : (
            <button className={`${styles.loginbtn}`} onClick={logInClick}></button>
            )}
            {!isLoggedIn && (
            <button className={`${styles.signbtn}`} onClick={signUpClick}></button>
            )}
          </div>
          <div className={styles.card}>
            <div className={styles.iconContainer}>
              <img src={upload_icon} alt="Upload" className={styles.icon} />
            </div>
            <h2><b> ?</b></h2>
            <p>  </p>
            <p> .</p>
            <button className={`${styles.btn}`} onClick={learnClick}></button>
          </div>
          <div className={styles.card}>
            <div className={styles.iconContainer}>
              <img src={download_icon} alt="Download" className={styles.icon} />
            </div>
            <h2><b> ?</b></h2>
            <p> </p> <p> .</p>
            <button className={`${styles.btn}`} onClick={() => navigate('/extract')}></button>
          </div>
          <Chatbot
            config={config}
            messageParser={MessageParser}
            actionProvider={ActionProvider}
          />
        </div>
      );
    }      
    

export default MainPage;
