import React, { useState, useRef } from 'react';
import { BeatLoader } from 'react-spinners';
import Modal from './Modal';
import styles from './FileUploadButton.module.css';
 
const serverURL = 'http://100.25.242.208:8080/';

function FileUploadButton() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [aiResponse, setAiResponse] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [reopen, setReopen] = useState(false);
  const [selectedFileName, setSelectedFileName] = useState('');

  const fileInputRef = useRef(null);  //     

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const reopenButton = () => {
    if (reopen){
      return <div>
        <button className={styles.reloadButton} onClick={() => setIsModalOpen(true)}> </button>
      </div>;
    }
    else return;
  };


  const handleImgUpload = (event) => {
    const file = event.target.files[0];
    if (file && file.type.startsWith('image/')) {
      setIsLoading(true);
      const formData = new FormData();
      formData.append('file', file);
      formData.append('token', 'DKU18son');

      const aiOcrURL = `${serverURL}ai/ocr`;
      fetch(aiOcrURL, {
        method: 'POST',
        body: formData,
      })
      .then(response => response.json())
      .then(data => {
        setAiResponse(data);
        setIsModalOpen(true);
        setReopen(true);
      })
      .catch((error)=> {
        console.error('Error:', error);
      })
      .finally(()=> {
        setIsLoading(false);
      });

      setSelectedFileName(file.name);
    }
  };

  const openFileDialog = () => {
    fileInputRef.current.click();
    //  " "   openFileDialog  , 
    //           
  };

  return (
    <div>
    <div>
      <input type="file" ref={fileInputRef} style={{ display: 'none' }} onChange={handleImgUpload} />
      {selectedFileName && <p><b><br />{selectedFileName}</b></p>}
      <button className={styles.uploadButton} onClick={openFileDialog}> </button>
      {isLoading ? (
        <div><BeatLoader /></div>
      ) : (<Modal isOpen={isModalOpen} onClose={closeModal} AiData={aiResponse} selectedFileName = {selectedFileName}>
        </Modal>
        )
    } 
    </div>
    {reopenButton()}
    </div>
  );
}

export default FileUploadButton;